import React from "react";
import "./Context.css";

import { Container } from "reactstrap";
import heroImg from "../../assests/images/hero-img1.png";

const Context = () => {
    return (
        <section>
            <Container className="context">
                <div className="container-left">
                    <img className="rightpic" src={heroImg} />
                    <div className="container-context-title">
                        <p><b>КІРІСПЕ</b></p>
                    </div>
                    <div className="container-context-description">
                        <p><a href="https://www.python.org/" title="Python — высокоуровневый язык программирования общего назначения с динамической строгой типизацией и автоматическим управлением памятью, ориентированный на повышение производительности разработчика, читаемости кода и его качества, а также на обеспечение переносимости написанных на нём программ."> Python</a> тілінің маңызды артықшылықтарының бірі – оқырмандар қолданатын модульдер мен пакеттердің үлкен кітапханасы. </p>
                        <p>
                            <a href="https://www.python.org/" title="Python — высокоуровневый язык программирования общего назначения с динамической строгой типизацией и автоматическим управлением памятью, ориентированный на повышение производительности разработчика, читаемости кода и его качества, а также на обеспечение переносимости написанных на нём программ."> Python</a>  - динамикалық семантикамен интерпретацияланған, объектіге бағытталған жоғары деңгейлі бағдарламалау  тілі. <a href="https://melimde.com/programmalaudi-oituda-informatika.html?page=3" title="Динамикалық типтеу - бағдарламалау тілдерінде қолданылатын тәсіл, онда айнымалының типі оны жариялау кезінде емес, оған мәнді меншіктеу кезінде анықталады."> Динамикалық типтеу </a> және байланыстырумен біріктірілген жоғары деңгейлі деректер құрылымы тілді қосымшаларды жылдам (RAD,<a href="https://en.wikipedia.org/wiki/Rapid_application_development" title="Rapid application development (RAD), also called rapid application building (RAB), is both a general term for adaptive software development approaches, and the name for James Martin's method of rapid development. In general, RAD approaches to software development put less emphasis on planning and more emphasis on an adaptive process. Prototypes are often used in addition to or sometimes even instead of design specifications.">Rapid Application Development</a>) әзірлеу үшін тартымды етеді. Сонымен қатар, оны бағдарламалық жасақтама компоненттерін байланыстыру үшін сценарий тілі ретінде пайдалануға болады. Python синтаксисін үйрену оңай, ол кодтың оқылуына ерекше мән береді және бұл бағдарламалық өнімдерді қолдау шығындарын азайтады. Python модульділікті және кодты қайта пайдалануды ынталандыру арқылы модульдер мен пакеттерді қолдайды. <a href="https://pythonchik.ru/osnovy/kak-rabotaet-python-interpretator" title="Интерпретатор — это программа, которая конвертирует ваши инструкции, написанные на Python, в байт-код и выполняет их. По сути интерпретатор — это программный слой между вашим исходным кодом и железом. Существует 2 типа интерпретаторов: Простой интерпретатор."> Python интерпретаторы</a> (аудармашысы) және үлкен стандартты кітапханасы барлық негізгі <a href="https://kk.wikipedia.org/wiki/%D0%9F%D0%BB%D0%B0%D1%82%D1%84%D0%BE%D1%80%D0%BC%D0%B0" title="Платформа (фр. plate – жайпақ, forme — форма) — геологияда қозғалмалығы біршама аз, жазық немесе үстірт тәрізді бедерлі, екі жікқабатты құрылымдық қабаттан – іргетастан және шөгінді тыстан тұратын жер қыртысының кең ауқымды атырабы. Жер бетінде іргетастық көрініс беретін бөліктерді қалқандар дейді. Шөгінді тыспен көмкерілген тұстарын тақта деп атайды. Архей мен протерозойда қалыптасқан ежелгі платформа (Шығыс Еуропа, Сібір) және ігретасы палеозой мен мезозой тау жыныстарьшан құралған жас платформа (Батыс Сібір, Скиф, Тұран) деп ажыратылады; кристалды тақтатастардан, гнейстерден, эффузивті жыныстардан және тағы басқалар. күшті метаморфозданған архейлік жыныстардан құралған жер қабығының көлемді берік алабы.[1] "> платформалар </a>үшін бастапқы және орындалатын кодтар түрінде ақысыз қол жетімді және оларды еркін таратуға болады.
                        </p>
                        <p>
                            Оқу барысында <a href="https://www.python.org/" title="Python — высокоуровневый язык программирования общего назначения с динамической строгой типизацией и автоматическим управлением памятью, ориентированный на повышение производительности разработчика, читаемости кода и его качества, а также на обеспечение переносимости написанных на нём программ."> Python</a> бағдарламалау ортасына берілген анықтаманың мәні ашылады, ал қәзір Python әмбебап бағдарламалау тілі екенін білу жеткілікті. Оның артықшылықтары мен кемшіліктері, сондай-ақ қолдану аясы бар. <a href="https://www.python.org/" title="Python — высокоуровневый язык программирования общего назначения с динамической строгой типизацией и автоматическим управлением памятью, ориентированный на повышение производительности разработчика, читаемости кода и его качества, а также на обеспечение переносимости написанных на нём программ."> Python</a> түсініктемесіне көптеген мәселелерді шешуге арналған кең стандартты кітапхана кіреді. Интернетте әр түрлі тақырыптар бойынша Python үшін сапалы кітапханалар бар: мәтіндерді өңдеу құралдары және интернет технологиялары, суреттерді өңдеу, қосымшаларды құру құралдары, мәліметтер базасына қол жеткізу механизмдері, ғылыми есептеулерге арналған пакеттер, графикалық интерфейсті құру кітапханалары және т.б. Сонымен қатар, Python-да аудармашыны осы тілдердегі бағдарламаларға енгізу (embedding) арқылы да, керісінше, <a href="https://www.python.org/" title="Python — высокоуровневый язык программирования общего назначения с динамической строгой типизацией и автоматическим управлением памятью, ориентированный на повышение производительности разработчика, читаемости кода и его качества, а также на обеспечение переносимости написанных на нём программ."> Python</a> бағдарламаларында осы тілдерде жазылған кітапханаларды пайдалану арқылы да C, <a href="https://ru.wikipedia.org/wiki/C%2B%2B" title="C++ — компилируемый, статически типизированный язык программирования общего назначения. Поддерживает такие парадигмы программирования, как процедурное программирование, объектно-ориентированное программирование, обобщённое программирование"> C++ </a>(және Java) тілдерімен интеграциялаудың қарапайым құралдары бар. Python тілі бірнеше бағдарламалау парадигмаларын қолдайды: императивті (<a href="https://kk.wikipedia.org/wiki/%D0%9F%D1%80%D0%BE%D1%86%D0%B5%D0%B4%D1%83%D1%80%D0%B0%D0%BB%D1%8B%D2%9B_%D2%B1%D1%81%D1%8B%D0%BD%D1%83" title="Процедуралық ұсыну (Процедурное представление; procedural representation) — сипаттау бірлігі процедура болатын алгоритмді сипаттау немесе білімдерді ұсыну тәсілі.[1] "> процедуралық</a>, құрылымдық, модульдік тәсілдер), объектіге бағытталған және функционалды бағдарламалау.
                        </p>
                    </div>
                </div>
            </Container>
        </section>
    );
};

export default Context;