import React from "react";
import "./Navmenu.css";

import { Container, Row, Col } from "reactstrap";

const Navmenu = (props) => {
    const { name } = props.item;

    return (
        <section>
            <Container className="navmenu">
                <div className="title-navmenu">
                    <p><b><center>{name}</center></b></p>
                </div>
                <header>
                    <div className="header-content">
                        <a href="#" className="logo"></a>
                        <label for="hamburger"><span></span></label>
                        <nav aria-label="main navigation">
                            <ul className="menus">
                                <li>
                                    <button
                                        type="button"
                                        aria-haspopup="true"
                                        aria-expanded="true"
                                        aria-controls="dropdown1"
                                    >
                                        Тапсырмалар<span className="arrow"></span>
                                    </button>
                                    <ul className="dropdown" id="dropdown1">
                                        <li><a href="#2">1 тапсырма</a></li>
                                        <li><a href="#6">2 тапсырма</a></li>
                                        <li><a href="#7">3 тапсырма</a></li>
                                    </ul>
                                </li>
                                <li>
                                    <button
                                        type="button"
                                        aria-haspopup="true"
                                        aria-expanded="true"
                                        aria-controls="dropdown1"
                                    >
                                        Есептер жинақтары<span className="arrow"></span>
                                    </button>
                                    <ul className="dropdown" id="dropdown1">
                                        <li><a href="#12">5 есеп жинағы</a></li>
                                        <li><a href="#15">6 есеп жинағы</a></li>
                                        <li><a href="#17">7 есеп жинағы</a></li>
                                    </ul>
                                </li>
                                <li>
                                    <button
                                        type="button"
                                        aria-haspopup="true"
                                        aria-expanded="true"
                                        aria-controls="dropdown1"
                                    >
                                        Қосымша тапсырма<span className="arrow"></span>
                                    </button>
                                    <ul className="dropdown" id="dropdown1">
                                        <li><a href="#16">8 тапсырма</a></li>
                                        <li><a href="#18">9 тапсырма</a></li>
                                        <li><a href="#10">10 тапсырма</a></li>
                                    </ul>
                                </li>
                            </ul>
                        </nav>
                    </div>
                </header>
            </Container>
        </section>
    );
};

export default Navmenu;
